<h2>开端<a class="header-anchor" id="%E5%BC%80%E7%AB%AF" href="#%E5%BC%80%E7%AB%AF">#</a></h2>
<p>最早听说「神农架」这个名字是通过电视的广告，却又很模糊，现在回想起来，也有相当长的时间没有见到过相关的广告了。一般而言的自然风景，对我来说是很难动脚前行的，这是因为我总是觉得这样的美景仅仅通过照片就足以领略，并没有什么需要亲身经历的。不过，这一次冬日前往神农架，也确实有难以遗忘的——神农架的雪。那里的温度可比武汉等地要低得多。</p>
<p>实际上这次去神农架来自于家人的邀约，虽然一开始并不想去，一是因为前面所说的，二是因为 2019 年 8 月已经去过一次。但最终畏于大年廿九晚上发生的种种事情，为了避免在初 <mjx-container class="MathJax" jax="SVG" style="direction: ltr; position: relative;"><svg style="overflow: visible; min-height: 1px; min-width: 1px; vertical-align: -0.025ex;" xmlns="http://www.w3.org/2000/svg" width="1.357ex" height="1.025ex" role="img" focusable="false" viewBox="0 -442 600 453" aria-hidden="true"><g stroke="currentColor" fill="currentColor" stroke-width="0" transform="scale(1,-1)"><g data-mml-node="math"><g data-mml-node="mi"><path data-c="1D45B" d="M21 287Q22 293 24 303T36 341T56 388T89 425T135 442Q171 442 195 424T225 390T231 369Q231 367 232 367L243 378Q304 442 382 442Q436 442 469 415T503 336T465 179T427 52Q427 26 444 26Q450 26 453 27Q482 32 505 65T540 145Q542 153 560 153Q580 153 580 145Q580 144 576 130Q568 101 554 73T508 17T439 -10Q392 -10 371 17T350 73Q350 92 386 193T423 345Q423 404 379 404H374Q288 404 229 303L222 291L189 157Q156 26 151 16Q138 -11 108 -11Q95 -11 87 -5T76 7T74 17Q74 30 112 180T152 343Q153 348 153 366Q153 405 129 405Q91 405 66 305Q60 285 60 284Q58 278 41 278H27Q21 284 21 287Z" style="stroke-width: 3;"></path></g></g></g></svg><mjx-assistive-mml unselectable="on" display="inline" style="top: 0px; left: 0px; clip: rect(1px, 1px, 1px, 1px); -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; position: absolute; padding: 1px 0px 0px 0px; border: 0px; display: block; width: auto; overflow: hidden;"><math xmlns="http://www.w3.org/1998/Math/MathML"><mi>n</mi></math></mjx-assistive-mml></mjx-container> 时上演类似的遭遇，我决定前去。</p>
<p>这篇杂记里，有些风景部分由于是 2019 年游历，已经忘却得差不多了，所以可能写的比较简略。重点应该是放在今年去的那一次。</p>
<h2>跟团和自驾<a class="header-anchor" id="%E8%B7%9F%E5%9B%A2%E5%92%8C%E8%87%AA%E9%A9%BE" href="#%E8%B7%9F%E5%9B%A2%E5%92%8C%E8%87%AA%E9%A9%BE">#</a></h2>
<p>这两次去神农架，恰好从两个方面体验了神农架的旅行体验：一个跟团，一个自驾。</p>
<p>2019 年去的那次是跟团旅行，好处是可以较为经济，较为完整地体验神农架。如果只是想去看看，并且想要看个完整，跟团<em>也许</em>是个好的选择（不过跟团一定是节省时间的最好选择）。跟团的问题在于你的旅行具有一定的限制，活动时间、作息安排、住宿安排等都会受到不同程度的限制。虽然正常情况下旅行团都不至于将你安排得过于寒碜（如果是，建议退出），生活条件也在选择范围之内，但其自由度远远不及自驾。</p>
<div class="notice tip"><p>跟团还有一个问题就是<strong>你很有可能旅行就只是旅行</strong>。这是什么意思呢？你可能旅行一趟下来根本不知道去了哪些地方。或者即使知道去了这些地方，也对路上的路一点头绪也没有。</p>
<p>如果你只打算去找个地方一次，那其实也没什么问题。但是这次第二次去，我深深感受到了我上一次跟团旅行所造成的这次路痴状况：什么地方都有印象，但又不知道这是哪里；去过所有的地方，但都不知道怎么走。</p>
</div>
<p>今年去的是自驾旅行。自驾最大的好处就是自己掌握，是一场有意义旅行的基础。如果你认为你并不想走马观花似地前往，就可以选择自驾。然而自驾造成的困难，最大的并不是旅途规划问题（因为都有资料可查，有向导可问），而是景区限制和消费问题。</p>
<div class="notice warning"><p>以下描述所针对的季节是冬天，对于夏天的情况可能有变。</p>
</div>
<p>比如这次我们开车准备上<strong>神农顶</strong>，景区规定<strong>如果外地司机没有四驱车则不允许进入</strong>；而本地司机可以直接开两驱车进入。这是赤裸裸的歧视啊！这一消息来自于「神农顶 · 大九湖」景区入口处的工作人员和我们吃饭地方的工作人员。根据吃饭地方的工作人员的描述，这样做的理由是本地人对山上路况更熟悉，水平更高！</p>
<p>如果没有四驱车，或者是单纯的散客，则必须去乘景区统一的巴士上山，然后统一时间下山。但是！自驾游就要有个自驾的样子，要把主动权掌握在自己的手里！所以我们就决定先回旅店，要么就随便玩玩然后回家8。</p>
<p>然而就在决定要遗憾而归的次日，我们想碰碰运气，于是又一次去到同一个入口处，人还是照样问是不是四驱，<strong>有没有防滑链</strong>（沿路都有卖的，大概价格参考：租用 ￥100，购买 ￥150，帮忙安装 ￥50）。我们故意回答的四驱。结果就真的让我们上了。笑晕，有点后悔昨天太老实了。</p>
<div class="notice danger"><p>玩归玩，安全还是要保证的。技术要过关也不是没有理由的。</p>
<p>上了山以后我发现和我们一样的两驱车不在少数；而有些吉普车高玩不带防滑链直接稳定上山。不过，有些两驱车即使带了防滑链，中途也在上山过程中侧滑了，差不多四五辆车前后堵在那里...甚至还差点被它滑下来撞了。</p>
<p>在这次去和回神农架的高速路上，我也目睹了两起车祸。一起是去时隧道里，两辆车头碰头相撞，前部直接不成形了，还有一辆彻底上下翻倒在地上......不知道司机怎么样了。回来的路上也是两辆车前部完全损坏。</p>
<p>总之，安全第一！</p>
</div>
<p>这些问题大概就是自驾要面对的。跟团的话就简单多啦。</p>
<h2>住宿、饮食和娱乐<a class="header-anchor" id="%E4%BD%8F%E5%AE%BF%E3%80%81%E9%A5%AE%E9%A3%9F%E5%92%8C%E5%A8%B1%E4%B9%90" href="#%E4%BD%8F%E5%AE%BF%E3%80%81%E9%A5%AE%E9%A3%9F%E5%92%8C%E5%A8%B1%E4%B9%90">#</a></h2>
