417T432 406T441 395T448 384T452 374T455 366L457 361L460 365Q463 369 466 373T475 384T488 397T503 410T523 422T546 432T572 439T603 442Q729 442 740 329Q741 322 741 190V104Q741 66 743 59T754 49Q775 46 803 46H819V0H811L788 1Q764 2 737 2T699 3Q596 3 587 0H579V46H595Q656 46 656 62Q657 64 657 200Q656 335 655 343Q649 371 635 385T611 402T585 404Q540 404 506 370Q479 343 472 315T464 232V168V108Q464 78 465 68T468 55T477 49Q498 46 526 46H542V0H534L510 1Q487 2 460 2T422 3Q319 3 310 0H302V46H318Q379 46 379 62Q380 64 380 200Q379 335 378 343Q372 371 358 385T334 402T308 404Q263 404 229 370Q202 343 195 315T187 232V168V108Q187 78 188 68T191 55T200 49Q221 46 249 46H265V0H257L234 1Q210 2 183 2T145 3Q42 3 33 0H25V46H41Z" style="stroke-width: 3;"></path></g></g></g></g></svg><mjx-assistive-mml unselectable="on" display="inline" style="top: 0px; left: 0px; clip: rect(1px, 1px, 1px, 1px); -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; position: absolute; padding: 1px 0px 0px 0px; border: 0px; display: block; width: auto; overflow: hidden;"><math xmlns="http://www.w3.org/1998/Math/MathML"><mn>3100</mn><mrow data-mjx-texclass="ORD"><mi mathvariant="normal">m</mi></mrow></math></mjx-assistive-mml></mjx-container>，但给我的感觉就是真的高原反应上来了。爬上去的过程特别的累，但上去一段时间就适应了，千万别放弃哦。</p>
<p>下面就是神农顶上的景象了。</p>
<p><img src="https://s4.ax1x.com/2022/02/07/HQ8Q1g.jpg" alt="">
<img src="https://s4.ax1x.com/2022/02/07/HQ8kXd.jpg" alt="">
<img src="https://s4.ax1x.com/2022/02/07/HQ81Xj.jpg" alt="">
<img src="https://s4.ax1x.com/2022/02/07/HQ8mAP.jpg" alt="">
<img src="https://s4.ax1x.com/2022/02/07/HQ8M9S.jpg" alt=""></p>
<p>登顶路上还有可以滑雪的地方。不过并不是单人滑雪，而是坐在橡皮舟子上划，看上去还挺好玩的，不过我没有划。这里的雪基本上都是粉状雪，也就是随便一搓就会变成单片雪花的那种，蹲一个形成原因的解释~ 有不少人在雪上写字，画爱心。在怂恿之下我也仿照着倒在一片特别平整（似强迫症福利）的雪上画人字。</p>
<p>远方的山上的山雾运动得特别快，在照片里得样子是只有一点点漫上来，等到我们出发时已经将那两座山之间的缝隙填满并且漫了过来，当我们驶过时就像驶进了大雾天气。</p>
<p>因为山上雪太多了，再加上有阳光，现场真的很刺眼。这导致我用 iPad Pro 拍出来的照片和视频有一大半都是不能见人的，因为这些反射光让拍出来的照片像蒙上了一层雾。只有少数几张（也就是上面那几张）包含了太阳，显示的更加清楚，但是整体上却又更暗了。所以这种专业场景还是得让摄像机来。</p>
